export default {
	data() {
		return {
			title: '指纹',
			result: '',
			disabled: true
		};
	},
	onLoad() {
















		this.disabled = false;
		this.result = '请在微信真机中使用，模拟器不支持';




	},
	methods: {
		fingerprint: function() {


































			wx.startSoterAuthentication({
				requestAuthModes: ['fingerPrint'],
				challenge: '123456',
				authContent: '请用指纹解锁',
				success(res) {
					uni.showToast({
						title: '识别成功',
						mask: false,
						duration: 1500
					});
				}
			});

		}
	}
};
